@charset "UTF-8";
* {
  box-sizing: border-box;
}

/* webkit specific styles */
input[type=color]::-webkit-color-swatch {
  border: none;
}

input[type=color]::-webkit-color-swatch-wrapper {
  padding: 0;
}

html,
body,
div,
span,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
abbr,
address,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
samp,
small,
strong,
sub,
sup,
var,
b,
i,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent;
  font-weight: inherit;
}

body {
  line-height: 1;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

nav ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}

a {
  margin: 0;
  padding: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent;
}

/* change colours to suit your needs */
mark {
  background-color: #ff9;
  color: #000;
  font-style: italic;
  font-weight: bold;
}

del {
  text-decoration: line-through;
}

abbr[title],
dfn[title] {
  border-bottom: 1px dotted;
  cursor: help;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

/* change border colour to suit your needs */
hr {
  display: block;
  height: 1px;
  border: 0;
  border-top: 1px solid #cccccc;
  margin: 1em 0;
  padding: 0;
}

input,
select {
  vertical-align: middle;
}

input:focus {
  outline: none;
}

ul,
ol {
  list-style-type: none;
}

body {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

main {
  display: block;
}

li {
  list-style: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 16px;
}

a {
  color: inherit;
  width: 100%;
  height: 100%;
  text-decoration: none;
}
a:hover {
  color: inherit;
}

img {
  vertical-align: middle;
  max-width: 100%;
  height: auto;
}

address {
  font-style: normal;
}

sup {
  vertical-align: super;
  font-size: smaller;
}

input,
button,
textarea,
select {
  display: block;
  color: #333;
  font-size: 16px;
  background: none;
  border: none;
  border-radius: 0;
  outline: none;
  width: 100%;
  margin: 0;
  padding: 0;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  -moz-appearance: textfield;
}
input:-webkit-autofill,
button:-webkit-autofill,
textarea:-webkit-autofill,
select:-webkit-autofill {
  color: #333;
  -webkit-box-shadow: 0 0 0px 1000px #fff inset;
}
input::placeholder,
button::placeholder,
textarea::placeholder,
select::placeholder {
  color: #b5b5b6;
}
input::-webkit-outer-spin-button, input::-webkit-inner-spin-button,
button::-webkit-outer-spin-button,
button::-webkit-inner-spin-button,
textarea::-webkit-outer-spin-button,
textarea::-webkit-inner-spin-button,
select::-webkit-outer-spin-button,
select::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input,
textarea,
select {
  background-color: #fff;
  border: solid 1px #d5d5d5;
  border-radius: 3px;
  padding: 6px 12px;
}
input[readonly],
textarea[readonly],
select[readonly] {
  color: rgba(51, 51, 51, 0.7);
  border: none;
  border-radius: 0;
}
input.__error,
textarea.__error,
select.__error {
  background-color: rgba(230, 52, 68, 0.1);
  border: solid 1px #e63444;
}
input.__secure,
textarea.__secure,
select.__secure {
  background-color: rgba(31, 218, 80, 0.05);
  border: solid 1px #1fda50;
}

textarea {
  min-height: 100px;
}

button {
  cursor: pointer;
}

input[type=checkbox], input[type=radio] {
  position: absolute;
  white-space: nowrap;
  width: 1px;
  height: 1px;
  overflow: hidden;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  margin: -1px;
}

input[type=radio] + label {
  margin-right: 20px;
  margin-bottom: 4px;
  padding-left: 28px;
  position: relative;
  cursor: pointer;
}
input[type=radio] + label::before {
  content: "";
  display: block;
  width: 20px;
  height: 20px;
  background-color: #f5f5f5;
  border: solid 1px #d5d5d5;
  border-radius: 50%;
  position: absolute;
  top: 2px;
  left: 0;
}
input[type=radio] + label::after {
  content: "";
  display: none;
  width: 14px;
  height: 14px;
  background-color: #014c86;
  border-radius: 50%;
  position: absolute;
  top: 5px;
  left: 3px;
}
input[type=radio]:checked + label::after {
  display: block;
}

input[type=checkbox] + label {
  margin-right: 20px;
  margin-bottom: 4px;
  padding-left: 28px;
  position: relative;
  cursor: pointer;
}
input[type=checkbox] + label::before {
  content: "";
  display: block;
  width: 20px;
  height: 20px;
  background-color: #f5f5f5;
  border: solid 1px #d5d5d5;
  border-radius: 2px;
  position: absolute;
  top: 2px;
  left: 0;
}
input[type=checkbox]:checked + label::before {
  background-color: #014c86;
  border-color: #014c86;
}

input[type=checkbox] + label::after {
  content: "";
  display: none;
  width: 6px;
  height: 10px;
  border: solid #fff;
  border-width: 0 2px 2px 0;
  position: absolute;
  top: 6px;
  left: 6px;
  transform: rotate(45deg);
}
input[type=checkbox]:checked + label::after {
  display: block;
}

select {
  cursor: pointer;
  padding-right: 32px;
  background-repeat: no-repeat;
  background-position: right 8px center;
  background-size: 10px 10px;
  background-image: url(data:image/png;base64,);
}

body,
input,
button,
textarea,
select {
  color: #333;
  font-size: 16px;
  font-family: "游ゴシック Medium", "游ゴシック体", "Yu Gothic Medium", YuGothic, "ヒラギノ角ゴ ProN", "Hiragino Kaku Gothic ProN", "メイリオ", Meiryo, "ＭＳ Ｐゴシック", "MS PGothic", sans-serif;
  line-height: 1.7;
}

