.l-outer {
  padding-left: 40px;
  padding-right: 40px;
}
@media screen and (max-width: 750px) {
  .l-outer {
    padding-left: 20px;
    padding-right: 20px;
  }
}

.l-inner {
  max-width: 1080px;
  margin-left: auto;
  margin-right: auto;
}

.c-section {
  padding-top: 80px;
  padding-bottom: 80px;
}

.p-header {
  height: 80px;
}
.p-header__inner {
  background-color: #fff;
  border-bottom: solid 1px #f5f5f5;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  padding-left: 20px;
}
.p-header-logo {
  width: 120px;
}
.p-header-logo a {
  display: block;
  transition: opacity 0.2s;
}
.p-header-logo a:hover {
  opacity: 0.7;
  transition: opacity 0.2s;
}
.p-header-menu-nav {
  margin-left: auto;
}
.p-header-menu-nav__lists {
  display: flex;
  align-items: center;
}
.p-header-menu-nav__item {
  color: #333;
}
.p-header-menu-nav__item:not(:first-of-type) {
  margin-left: 20px;
}
.p-header-menu-nav__item a {
  display: block;
  color: #333;
  font-size: 15px;
  font-weight: bold;
  padding: 10px 10px;
}
.p-header-drawer-btn {
  background-color: #014c86;
  width: 80px;
  height: 80px;
  margin-left: 40px;
  position: relative;
  transition: opacity 0.2s;
}
.p-header-drawer-btn:hover {
  opacity: 0.9;
  transition: opacity 0.2s;
}
.p-header-drawer-btn::before, .p-header-drawer-btn::after,
.p-header-drawer-btn span {
  content: "";
  display: block;
  background-color: #fff;
  width: 28px;
  height: 2px;
  transition: opacity 0.2s, transform 0.2s, margin 0.2s;
}
.p-header-drawer-btn::before {
  margin-top: -6px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.p-header-drawer-btn[data-active=true]::before {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) rotate(45deg);
  margin: 0;
}

.p-header-drawer-btn::after {
  margin-top: 6px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.p-header-drawer-btn[data-active=true]::after {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) rotate(-45deg);
  margin: 0;
}

.p-header-drawer-btn span {
  margin: auto;
}
.p-header-drawer-btn[data-active=true] span {
  opacity: 0;
}

.p-header-drawer-menu-bg {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.5s, visibility 0.5s;
  z-index: -1;
}
.p-header-drawer-menu-bg[data-active=true] {
  opacity: 1;
  visibility: visible;
  background-color: rgba(51, 51, 51, 0.7);
}
.p-header-drawer-menu-bg[data-active=false] {
  background-color: rgba(51, 51, 51, 0.7);
}
.p-header-drawer-menu-nav {
  position: fixed;
  top: 0;
  right: 0;
  background-color: #fff;
  width: 90%;
  max-width: 400px;
  height: 100%;
  box-shadow: -2px 16px 16px rgba(0, 0, 0, 0.1);
  transition: right 0.9s;
}
.p-header-drawer-menu-nav[data-active=true] {
  right: 0;
  transition: right 0.6s;
}
.p-header-drawer-menu-nav[data-active=false] {
  right: -100%;
}
.p-header-drawer-menu-nav__close-btn {
  background-color: #333;
  width: 80px;
  height: 80px;
  margin-left: auto;
  position: relative;
  transition: opacity 0.2s;
}
.p-header-drawer-menu-nav__close-btn:hover {
  opacity: 0.9;
  transition: opacity 0.2s;
}
.p-header-drawer-menu-nav__close-btn::before, .p-header-drawer-menu-nav__close-btn::after {
  content: "";
  display: block;
  background-color: #f5f5f5;
  width: 28px;
  height: 2px;
}
.p-header-drawer-menu-nav__close-btn::before {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) rotate(45deg);
}
.p-header-drawer-menu-nav__close-btn::after {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) rotate(-45deg);
}
.p-header-drawer-menu-nav__lists {
  border-top: solid 1px #f5f5f5;
}
.p-header-drawer-menu-nav__item {
  color: #333;
  border-bottom: solid 1px #f5f5f5;
}
.p-header-drawer-menu-nav__item a {
  display: block;
  color: #333;
  font-size: 15px;
  font-weight: bold;
  padding: 20px 20px;
  position: relative;
}
.p-header-drawer-menu-nav__item a::before {
  content: "";
  display: block;
  width: 6px;
  height: 6px;
  border-top: solid 2px #014c86;
  border-left: solid 2px #014c86;
  transform: rotate(135deg);
  position: absolute;
  top: 0;
  bottom: 0;
  right: 20px;
  z-index: 1;
  margin: auto;
  transition: right 0.2s ease-in-out;
}
.p-header-drawer-menu-nav__item a:hover::before {
  right: 18px;
  transition: right 0.2s ease-in-out;
}

.p-header-drawer-menu-nav__item a::after {
  content: "";
  display: block;
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  background-color: #f5f5f5;
  opacity: 0.5;
  transform: scaleX(0);
  transform-origin: right;
  transition: transform 0.2s ease-in-out, opacity 0.2s ease-in-out;
}
.p-header-drawer-menu-nav__item a:hover::after {
  transform: scaleX(1);
  transform-origin: left;
  opacity: 1;
}

.p-header-drawer-menu-nav__item a span {
  position: relative;
  z-index: 1;
}

.p-footer {
  background-color: #333;
  padding: 20px 20px;
}
.p-footer__copyright {
  color: #fff;
  font-size: 12px;
  text-align: center;
}

